<template>
  <div>
    <div class="echarts_box" id="mainTwo"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import "echarts/src/chart/pie";
import "echarts-gl";
export default {
    data() {
        return {
            optionData: [
        {
          name: "",
          value: 50,
          itemStyle: {
            opacity: 1,
            color: "#4d7dd2",
          },
        },
        {
          name: "",
          value: 25,
          itemStyle: {
            opacity: 1,
            color: "#fd8839",
          },
        },
        {
          name: "/",
          value: 25,
          itemStyle: {
            opacity: 1,
            color: "#b2b2b2",
          },
        },
      ],
        };
    },
    mounted(){
        this.echartsPie()
    },
   methods:{
    echartsPie() {
      let hoveredIndex = "";
      // let chartDom = this.$refs["basicDataPie"];
      let myChart = echarts.init(document.getElementById("mainTwo"));
      const getPie3D = (pieData, internalDiameterRatio) => {
        //internalDiameterRatio:
        let series = [];
        let sumValue = 0;
        let startValue = 0;
        let endValue = 0;
        let k = 1;
        pieData.sort((a, b) => {
          return b.value - a.value;
        });
        //  series-surface 
        for (let i = 0; i < pieData.length; i++) {
          sumValue += pieData[i].value;
          let seriesItem = {
            name:
              typeof pieData[i].name === "undefined"
                ? `series${i}`
                : pieData[i].name,
            type: "surface",
            parametric: true,
            wireframe: {
              show: false,
            },
            pieData: pieData[i],
            pieStatus: {
              selected: false,
              hovered: false,
              k: k,
            },
            radius: "50%",
            center: ["10%", "10%"],
          };

          if (typeof pieData[i].itemStyle != "undefined") {
            let itemStyle = {};
            typeof pieData[i].itemStyle.color != "undefined"
              ? (itemStyle.color = pieData[i].itemStyle.color)
              : null;
            typeof pieData[i].itemStyle.opacity != "undefined"
              ? (itemStyle.opacity = pieData[i].itemStyle.opacity)
              : null;
            seriesItem.itemStyle = itemStyle;
          }
          series.push(seriesItem);
        }

        //  sumValue getParametricEquation 
        //  series-surface  series-surface.parametricEquation
        for (let i = 0; i < series.length; i++) {
          endValue = startValue + series[i].pieData.value;
          series[i].pieData.startRatio = startValue / sumValue;
          series[i].pieData.endRatio = endValue / sumValue;
          series[i].parametricEquation = getParametricEquation(
            series[i].pieData.startRatio,
            series[i].pieData.endRatio,
            false,
            false,
            k,
            series[i].pieData.value
          );
          startValue = endValue;
        }
        let boxHeight = getHeight3D(series, 10); //3d/2626px
        //  legendDataseries 
        let option = {
          backgroundColor: "#fff",
          tooltip: {
            backgroundColor: "black",
            formatter: (params) => {
              if (
                params.seriesName !== "mouseoutSeries" &&
                params.seriesName !== "pie3d"
              ) {
                let bfb = (
                  (option.series[params.seriesIndex].pieData.endRatio -
                    option.series[params.seriesIndex].pieData.startRatio) *
                  100
                ).toFixed(2);
                return (
                  `${params.seriesName}<br/>` +
                  `<span style="display:inline-block;margin-right:5px;border-radius:10px;width:10px;height:10px;background-color:${params.color};"></span>` +
                  `${bfb}%`
                );
              }
            },
          },
          legend: {
            bottom: "3%",
            itemGap: 20,
            icon: "path://M0,12L12,12C12,5.37258,6.62742,0,0,0L0,12Z",
          },
          title: {
            text: "",
            left: "center",
            top: "2%",
            textStyle: {
              fontWeight: 500,
              fontSize: 16,
              color: "#3D3D3D",
            },
          },
          xAxis3D: {
            min: -1,
            max: 1,
          },
          yAxis3D: {
            min: -1,
            max: 1,
          },
          zAxis3D: {
            min: -1,
            max: 1,
          },
          grid3D: {
            show: false,
            boxHeight: boxHeight,
            top: "-30",
            left: "10",
            bottom: "60%",
            environment: "auto",
            viewControl: {
              distance: 300, //
              alpha: 25, //X
              beta: -70, //Y
              autoRotate: false, //
            },
          },
          series: series,
        };
        return option;
      };

      //3d
      const getHeight3D = (series, height) => {
        series.sort((a, b) => {
          return b.pieData.value - a.pieData.value;
        });
        return (height * 15) / series[0].pieData.value;
      };

      //  series-surface.parametricEquation
      const getParametricEquation = (
        startRatio,
        endRatio,
        isSelected,
        isHovered,
        k,
        h
      ) => {
        // 
        let midRatio = (startRatio + endRatio) / 2;
        let startRadian = startRatio * Math.PI * 2;
        let endRadian = endRatio * Math.PI * 2;
        let midRadian = midRatio * Math.PI * 2;
        // 
        if (startRatio === 0 && endRatio === 1) {
          isSelected = false;
        }
        // / k 1/3
        k = typeof k !== "undefined" ? k : 1 / 3;
        //  x y  0
        let offsetX = isSelected ? Math.cos(midRadian) * 0.1 : 0;
        let offsetY = isSelected ? Math.sin(midRadian) * 0.1 : 0;
        //  1
        let hoverRate = isHovered ? 1.05 : 1;
        // 
        return {
          u: {
            min: -Math.PI,
            max: Math.PI * 2,
            step: Math.PI / 32,
          },
          v: {
            min: 0,
            max: Math.PI * 2,
            step: Math.PI / 80,
          },
          x: function (u, v) {
            if (u < startRadian) {
              return (
                offsetX +
                Math.cos(startRadian) * (1 + Math.cos(v) * k) * hoverRate
              );
            }
            if (u > endRadian) {
              return (
                offsetX +
                Math.cos(endRadian) * (1 + Math.cos(v) * k) * hoverRate
              );
            }
            return offsetX + Math.cos(u) * (1 + Math.cos(v) * k) * hoverRate;
          },
          y: function (u, v) {
            if (u < startRadian) {
              return (
                offsetY +
                Math.sin(startRadian) * (1 + Math.cos(v) * k) * hoverRate
              );
            }
            if (u > endRadian) {
              return (
                offsetY +
                Math.sin(endRadian) * (1 + Math.cos(v) * k) * hoverRate
              );
            }
            return offsetY + Math.sin(u) * (1 + Math.cos(v) * k) * hoverRate;
          },
          z: function (u, v) {
            if (u < -Math.PI * 0.5) {
              return Math.sin(u);
            }
            if (u > Math.PI * 2.5) {
              return Math.sin(u) * h * 0.1;
            }
            return Math.sin(v) > 0 ? 1 * h * 0.1 : -1;
          },
        };
      };

      let option = getPie3D(this.optionData, 0.8);
      //label2dlabelLine3dsetOption
      option.series.push({
        // name: "pie3d",
        type: "pie",
        label: {
          color: "black",
          fontSize: 16,
          //  position: 'inner',
          // formatter: "{b}\n\n",
          // padding: [0, -40],
          formatter: (item) => {
            //  console.log(item)
            return item.data.name + ":" + item.data.value + "";
          },
        },
        labelLine: {
          showAbove: false,
          length: 20, // 
          length2: 40, // 
          lineStyle: {
            color: "#686868", // 
            width: 1,
            type: "solid", // 
          },
        },
        data: this.optionData,
        // startAngle: -25, //[0, 360]
        clockwise: false, //3d
        radius: ["65%", "65%"],
        center: ["55%", "48%"], //
        itemStyle: {
          opacity: 0,
        },
      });
      option && myChart.setOption(option);

      myChart.on("mouseover", function (params) {
        // 
        let isSelected;
        let isHovered;
        let startRatio;
        let endRatio;
        let k;
        let i;

        //  mouseover 
        if (hoveredIndex === params.seriesIndex) {
          return;

          // 
        } else {
          //  option 
          if (hoveredIndex !== "") {
            //  option.series  false
            isSelected = option.series[hoveredIndex].pieStatus.selected;
            isHovered = false;
            startRatio = option.series[hoveredIndex].pieData.startRatio;
            endRatio = option.series[hoveredIndex].pieData.endRatio;
            k = option.series[hoveredIndex].pieStatus.k;
            i =
              option.series[hoveredIndex].pieData.value ===
              option.series[0].pieData.value
                ? 35
                : 10;
            //  option 
            option.series[hoveredIndex].parametricEquation =
              getParametricEquation(
                startRatio,
                endRatio,
                isSelected,
                isHovered,
                k,
                i
              );
            option.series[hoveredIndex].pieStatus.hovered = isHovered;

            //  seriesIndex 
            hoveredIndex = "";
          }

          //  mouseover  option 
          if (
            params.seriesName !== "mouseoutSeries" &&
            option.series[params.seriesIndex].pieStatus
          ) {
            //  option.series  true
            isSelected = option.series[params.seriesIndex].pieStatus.selected;
            isHovered = true;
            startRatio = option.series[params.seriesIndex].pieData.startRatio;
            endRatio = option.series[params.seriesIndex].pieData.endRatio;
            k = option.series[params.seriesIndex].pieStatus.k;

            //  option 
            option.series[params.seriesIndex].parametricEquation =
              getParametricEquation(
                startRatio,
                endRatio,
                isSelected,
                isHovered,
                k,
                option.series[params.seriesIndex].pieData.value + 5
              );
            option.series[params.seriesIndex].pieStatus.hovered = isHovered;

            //  seriesIndex
            hoveredIndex = params.seriesIndex;
          }

          //  option
          myChart.setOption(option);
        }
      });
    },
   },

}
</script>

<style lang="scss" scoped>
.echarts_box{
    width:100vw;
    height: 500px;
}
</style>